import React, {Component} from 'react';
import { Container, Row, Col } from 'react-grid-system';
//images
import profileimage1 from './images/prof1.jpeg';
import profileimage2 from './images/prof2.jpeg';
//icons
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebookF, faLinkedinIn, faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
//animations
import Fade from 'react-reveal/Fade';


class ProfileImg extends Component {
  constructor(props){
    super(props);
    this.toggleImg = this.toggleImg.bind(this);
    this.state = {showprofpic: true};
  }
  toggleImg(){
    this.setState({showprofpic: !this.state.showprofpic });
  }
  render() {
    return (
      <div className="about-img" ><img src={this.state.showprofpic ? profileimage2 : profileimage1} onClick={this.toggleImg} alt="me!"/><div className="caption"><small>{this.state.showprofpic ? "click the image for a cooler one!" : null}</small></div></div>
    );
  }
}

class About extends Component {
  render() {
    return (
      <div className="about">
            <Container>
              <Row>
                <Col sm={4}>
                  <Fade>
                  <div className="media"> 
                  <ProfileImg/>
                  </div>
                  </Fade>
                </Col>
                <Col sm={8}>
                  <Fade>
                  <header>Hello!</header>
                  <p>My name is Megan Ung and I am currently a sophomore studying computer science at Carnegie Mellon University. I work as a research assistant at the <a target="_blank" rel="noopener noreferrer" href="https://cups.cs.cmu.edu/">CyLab Usable Privacy and Security Lab</a> at CMU. I am also interested in the intersection between technology and biology (thinking about pursuing a computational biology minor!). I work with <a target="_blank" rel="noopener noreferrer" href="https://women.cs.cmu.edu">Women@SCS</a>, <a target="_blank" rel="noopener noreferrer" href="https://www.scs4all.cs.cmu.edu/">SCS4ALL</a>, and <a target="_blank" rel="noopener noreferrer" href="https://www.cmu.edu/scs/technights/">TechNights</a> to help make tech an inclusive community- on campus, off campus, and in the future. 
                  </p>
                  </Fade>
                </Col>
              </Row>
            </Container>
            <Fade>
            <div className="icons">
              <a className="icon" target="_blank" rel="noopener noreferrer" href="https://www.facebook.com/meganung"><FontAwesomeIcon icon={faFacebookF} /></a>
              <a className="icon" target="_blank" rel="noopener noreferrer" href="https://www.linkedin.com/in/meganung"><FontAwesomeIcon icon={faLinkedinIn} /></a>
              <a className="icon" target="_blank" rel="noopener noreferrer" href="mailto:ung.meg@gmail.com"><FontAwesomeIcon icon={faEnvelope} /></a>
              <a className="icon" target="_blank" rel="noopener noreferrer" href="https://instagram.com/meganung"><FontAwesomeIcon icon={faInstagram} /> </a>
            </div>
            </Fade>
            <Fade>
            <div className="funnings">
              <p>Check out my <a target="_blank" rel="noopener noreferrer" href="https://instagram.com/eatingsfrom">eatingsfrom</a> all over the world! Besides eating, I love staying active through swimming, hiking, and skiing. I also enjoy crafting (origami, crocheting, and more!). </p>
            </div>
            </Fade>
          </div>
    );
  }
}

export default About;

