import React, { Component } from 'react';
import './App.css';
import About from './About';
import Projects from './Projects';
//grid
import { setConfiguration } from 'react-grid-system';
//animation
import Pulse from 'react-reveal/Pulse';
import Fade from 'react-reveal/Fade';

setConfiguration({ defaultScreenClass: 'sm', gridColumns: 12, gutterWidth: 30 });


var tabList = [
    { 'id': 0, 'name': 'ABOUT', 'url': '#about' },
    { 'id': 1, 'name': 'PROJECTS', 'url': '#projects' }
];

class Tab extends Component{
  constructor(props){
    super(props);
    this.state = {isCurrent: false}
  }
    
  render(){
    return (
      <li className={this.props.isCurrent ? 'current-tab' : null}>
        <a onClick={this.props.handleClick} href={this.props.url}>
        {this.props.name}
        </a>
      </li>
    );
  }
}

class Tabs extends Component{
    handleClick(tab){
    	this.props.changeTab(tab);
    }
    render(){
        return (
            <Fade>
            <nav>
                <ul>
                {this.props.tabList.map(function(tab) {
                    return (
                        <Tab
                            handleClick={this.handleClick.bind(this, tab)}
                            key={tab.id}
                            url={tab.url}
                            name={tab.name}
                            isCurrent={(this.props.currentTab === tab.id)}
                         />
                    );
                }.bind(this))}
                </ul>
            </nav>
            </Fade>
        );
    }
}

class HeaderName extends Component {
  render() {
    return (
      <div >
        <Pulse>
        <a className="HeaderName"
          href="." 
        >Megan ung
        </a>  
        </Pulse>
      </div>
    );
  }
}

class Content extends Component{
  render(){
    return(
      <div className="content">
      	{this.props.currentTab === 0 ?
          <About />
        :null}

        {this.props.currentTab === 1 ?
          <Projects />
        :null}
      </div>
    );
  }
}

class App extends Component{
  constructor(props) {
    super(props);
    this.state = {
      tabList: tabList,
      currentTab: 0
    }
  }
  changeTab(tab){
    this.setState({ currentTab: tab.id });
    setConfiguration({ defaultScreenClass: 'sm', gridColumns: 12, gutterWidth: tab.id === 0 ? 30 : 60 });
  }
  render(){
    return(
      <div className='App'>
        <div className='Header'>        
          <HeaderName />
          <Tabs
            currentTab={this.state.currentTab}
            tabList={this.state.tabList}
            changeTab={this.changeTab.bind(this)}
          />
        </div>
        <Content 
          currentTab={this.state.currentTab} 
        />
      </div>
    );
  }
}

export default App;
